<template>
  <div id="app">
    <div id="chat-area">
      <div class="messages-area" id="scroll" :style="{ 'overflow-y': showInput ? 'auto' : 'hidden' }" v-if="chatOpened"
        ref="messagesRef">
        <div class="message" :key="key" v-for="(msg, key) in filteredMessages">
          <span 
            class="message-type" 
            v-bind:class="{
              'chamado': msg.type === 'chamado',
              'advertencia': msg.type === 'advertencia',
              'Admin': msg.type === 'Admin',
              'ajuda': msg.type === 'ajuda',
              'megaphone': msg.type === 'megaphone'
            }"
            :style="msg.custom && msg.custom"
          >
            {{ msg.type }}
          </span>
          <div 
            class="message-content" 
            v-bind:class="{
              'chamado': msg.type === 'chamado',
              'advertencia': msg.type === 'advertencia',
              'Admin': msg.type === 'Admin',
              'ajuda': msg.type === 'ajuda',
              'megaphone': msg.type === 'megaphone',
            }"
            :style="msg.custom && msg.custom"
          >
            <span class="message-sendedBy">{{ msg.author }}:</span>
            {{ msg.content }}
              <button 
                class="delete"
                v-show="isAdmin"
                @click="() => deleteMessage(msg.content)"
              >
              <img src="./assets/images/bin.svg" />
            </button>
          </div>
        </div>
      </div>
      <div id="input-area" v-if="showInput">
        <div class="input-box">
          <div class="commandPrompt">
            <input type="text" maxlength="250" spellcheck="false" class="input" v-model="message" autofocus
              @keypress.enter.prevent="send" ref="input" placeholder="Digite sua mensagem aqui..." />
          </div>
          <img src="./assets/images/emoji.svg" :style="{ opacity: emojiPickerOpened ? '1' : '.5' }"
            @click="emojiPickerOpened = !emojiPickerOpened" class="emoji" />
          <img src="./assets/images/send.svg" @click="send" class="send" />
        </div>
        <div class="notify-box" @click="changeNotify">
          <img :style="{ width: notifyToggled ? '0.833vw' : '1.146vw' }" :src="
            notifyToggled
              ? require('./assets/images/notify_on.svg')
              : require('./assets/images/notify_off.svg')
          " />
        </div>
      </div>
      <div class="filter-types-area" v-if="showInput">
        <button class="chatTypeBackground" @click="() => changeChatType('all')"
          :class="{ selected: selectedChatType === 'all' }">
          Todos
        </button>
        <button class="chatTypeBackground" @click="() => changeChatType('global')"
          :class="{ selected: selectedChatType === 'global' }">
          Global
        </button>
        <button class="chatTypeBackground" @click="() => changeChatType('ajuda')"
          :class="{ selected: selectedChatType === 'ajuda' }">
          Ajuda
        </button>
        <button class="chatTypeBackground" @click="() => changeChatType('local')"
          :class="{ selected: selectedChatType === 'local' }">
          Local
        </button>
        <button class="chatTypeBackground" v-for="(type, key) in chatTypes" :key="key" @click="() => changeChatType(type)"
          :class="{ selected: selectedChatType === type }">
          {{ type }}
        </button>
        <div class="emoji-area" v-show="emojiPickerOpened">
          <div class="emoji-item" @click="() => selectEmoji(emoji)" :key="k" v-for="(emoji, k) in emojiList">
            {{ emoji }}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import emojis from "./emojis.json";
import post from "./utils/post";

export default {
  data() {
    return {
      emojiPickerOpened: false,
      emojiList: emojis[1].split(" "),
      blockedMessages: [],
      chatOpened: false,
      showInput: false,
      chatTypes: [],
      isAdmin: false,
      selectedChatType: "all",
      listener: null,
      input: null,
      notifyToggled: true,
      message: "",
      messages: [],
      messagesRef: null,
      cachedMessages: [],
      currCachedMsg: 1,
    };
  },

  watch: {
    messages: {
      handler() {
        if (this.showChatTimer) {
          clearTimeout(this.showChatTimer);
        }

        if (this.notifyToggled) {
          this.chatOpened = true;
          setTimeout(() => {
            this.$nextTick(() => {
              const messagesObj = this.$refs.messagesRef;
              messagesObj.scrollTop = messagesObj.scrollHeight;
            });
          }, 125);
        }
        this.resetShowChatTimer();
      },
      deep: true,
    },
  },

  computed: {
    filteredMessages() {
      return this.selectedChatType !== "all"
        ? this.messages.filter((el) => ((el.type === this.selectedChatType) || (el.type === "megaphone")))
        : this.messages;
    },
  },

  methods: {
    deleteMessage(content) {
      post("ChatDelete", {
        message: content
      });
    },

    changeChatType(type) {
      this.selectedChatType = type;
      const messagesObj = this.$refs.messagesRef;
      this.$nextTick(() => {
        messagesObj.scrollTop = messagesObj.scrollHeight;
      });
    },

    isCommand() {
      return (this.message[0] === "/");
    },

    isCommandPreview() {
      if (this.message.includes("!staff")) {
        return "Admin";
      } else if (this.message.includes("!gr")) {
        return this.chatTypes[1];
      } else if (this.selectedChatType == "all") {
        return "local";
      } else {
        return this.selectedChatType;
      }
    },

    changeNotify() {
      this.notifyToggled = !this.notifyToggled;
    },

    checkBlockedMessages() {
      let hasBlocked = false;
      this.message.split(" ").map((v) => {
        this.blockedMessages.forEach((v) => {
          if (this.message.includes(v)) {
            hasBlocked = true;
            return;
          }
        });
        if (hasBlocked) return;
      });

      return !hasBlocked;
    },

    checkFlood() {
      if (this.isCommand() || this.isAdmin) {
        return true;
      }
      let hasFlood = false;
      this.cachedMessages.map((v) => {
        if (v === this.message) {
          hasFlood = true;
          return;
        }
      });

      return !hasFlood;
    },

    send() {
      if (this.message !== "") {
        if (this.checkBlockedMessages() && this.checkFlood()) {
          if (this.cachedMessages.length > 5) this.cachedMessages.shift();
          this.cachedMessages.push(this.message);

          if (this.message.includes("!staff")) {
            this.message = this.message.replace("!staff", "");
            this.selectedChatType = "Admin";
            setImmediate(() => {
              this.selectedChatType = "all";
            });
          }

          if (this.message.includes("!global") || this.message.includes("!gl")) {
            this.message = this.message.replace("!global", "");
            this.message = this.message.replace("!gl", "");
            this.selectedChatType = "global";
            setImmediate(() => {
              this.selectedChatType = "global";
            });
          }

          if (this.message.includes("!ajuda") || this.message.includes("!aj")) {
            this.message = this.message.replace("!ajuda", "");
            this.message = this.message.replace("!aj", "");
            this.selectedChatType = "ajuda";
            setImmediate(() => {
              this.selectedChatType = "ajuda";
            });
          }

          if (this.message.includes("!gr")) {
            this.message = this.message.replace("!gr", "");
            this.selectedChatType = this.chatTypes[1];
            setImmediate(() => {
              this.selectedChatType = "all";
            });
          }

          post("ChatSubmit", {
            message: this.message,
            tag: this.isCommandPreview(),
          });

          this.message = "";
          this.hideInput();
        }
      } else {
        this.hideInput();
      }
    },

    keyUp() {
      if (
        this.currCachedMsg > 5 ||
        this.currCachedMsg > this.cachedMessages.length
      )
        return;
      this.message =
        this.cachedMessages[this.cachedMessages.length - this.currCachedMsg];
      this.currCachedMsg++;
    },

    keyDown() {
      if (this.currCachedMsg <= 1) return;
      this.currCachedMsg--;
      this.message =
        this.cachedMessages[this.cachedMessages.length - this.currCachedMsg];
    },

    clear() {
      this.messages = [];
    },

    selectEmoji(emoji) {
      this.message += emoji;
      this.emojiPickerOpened = false;
    },

    clearShowChatTimer() {
      clearTimeout(this.showChatTimer);
    },

    resetShowChatTimer() {
      this.clearShowChatTimer();
      this.showChatTimer = setTimeout(() => {
        if (!this.showInput) {
          this.chatOpened = false;
        }
      }, 7000);
    },

    hideInput() {
      this.message = "";
      this.showInput = false;
      this.resetShowChatTimer();
      post("close");
    },
  },

  destroyed() {
    clearInterval(this.focusTimer);
    window.removeEventListener("message", this.listener);
  },

  mounted() {
    fetch("http://chat/getCityName").then((data) => {
      const el = document.querySelector(".selected")
      if (!el) {
        return;
      }
      if (data === "candy") {
        el.style.background = "#04B5B9";
      } else {
        el.style.background = "#c38a10";
      }
    });

    this.listener = window.addEventListener("message", (event) => {
      const item = event.data || event.detail;

      switch (item.Action) {
        case "Chat":
          this.chatOpened = true;
          this.showInput = true;
          this.currCachedMsg = 1;
          this.chatTypes = item.Data;
          this.blockedMessages = item.Block;
          this.isAdmin = this.chatTypes.includes("Admin")
          setTimeout(() => {
            this.$nextTick(() => {
              this.$refs.input.focus();
              this.$refs.messagesRef.scrollTop =
                this.$refs.messagesRef.scrollHeight;
            });
          }, 125);
          break;

        case "Message":
          this.messages.push({
            type: item.Type,
            author: item.Author,
            content: item.Message,
            custom: item.Custom,
          });
          break;
        case "Delete":
          this.messages = this.messages.filter((el) => el.content !== item.Message)
          break;

        default:
          break;
      }
    });

    window.addEventListener("keydown", (event) => {
      if (event.code == "Escape") {
        this.hideInput();
      } else if (event.code == "ArrowUp") this.keyUp();
      else if (event.code == "ArrowDown") this.keyDown();
    });
  },
};
</script>

<style scoped>
#app {
  width: 100vw;
  height: 100vh;
}

#chat-area {
  position: absolute;
  top: 2.031vw;
  left: 1.771vw;
  display: flex;
  flex-direction: column;
  width: 25.573vw;
}

.messages-area {
  display: flex;
  flex-direction: column;
  gap: 0.26vw;
  max-height: 11.563vw;
  overflow-y: auto;
  direction: rtl;
  padding-left: 0.417vw;
  width: 100%;
  overflow-x: hidden;
}

.message {
  width: 100%;
  display: flex;
  align-items: flex-start;
  height: 100%;
  direction: ltr;
  position: relative;
}

.message-type {
  background: rgba(0, 0, 0, 0.5);
  border-top-left-radius: 0.208vw;
  border-bottom-left-radius: 0.208vw;
  font-weight: 500;
  color: #fff;
  font-size: 0.625vw;
  padding: 0.313vw 0.729vw;
  text-transform: uppercase;
  height: 100%;
  letter-spacing: 0.031vw;
}

.message-content {
  width: max-content;
  background: linear-gradient(90deg,
      rgba(0, 0, 0, 0.4) 0%,
      rgba(0, 0, 0, 0.2) 100%);
  border-top-right-radius: 0.208vw;
  border-bottom-right-radius: 0.208vw;
  font-weight: 300;
  font-size: 0.625vw;
  color: rgba(255, 255, 255, 0.9);
  padding: 0.313vw 0.729vw;
  letter-spacing: 0.031vw;
}

.message-sendedBy {
  color: hsl(0deg 0% 100% / 90%);
}

.message button.delete {
  display: none;
  background: #c38a10;
  border: none;
  outline: none;
  cursor: pointer;
  width: 1.25vw;
  height: 1.25vw;
  margin-left: 0.208vw;
  position: absolute;
  right: 0;
  bottom: 0;
  border-top-left-radius: 0.208vw;
  border-bottom-right-radius: 0.208vw;
  opacity: 0.9;
}

.message button.delete img {
  width: 0.677vw;
}

.message:hover button.delete {
  display: flex;
  justify-content: center;
  align-items: center;
}

#input-area {
  display: flex;
  gap: 0.313vw;
  margin-top: 1.094vw;
}

.input-box {
  width: 23.177vw;
  height: 2.083vw;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 0.208vw;
  padding: 0 0.729vw;
  display: flex;
  gap: 1.302vw;
}

.input {
  flex: 1;
  margin-left: 0.223vw;
  background: none;
  outline: none;
  border: none;
  font-weight: 500;
  font-size: 0.625vw;
  color: #ebebeb;
}

.input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.emoji {
  width: 0.938vw;
  cursor: pointer;
}

.send {
  width: 0.677vw;
  cursor: pointer;
}

.notify-box {
  background: rgba(0, 0, 0, 0.5);
  border-radius: 0.208vw;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.083vw;
  cursor: pointer;
}

.notify-box img {
  width: 0.833vw;
}

.filter-types-area {
  display: flex;
  margin-top: 0.677vw;
  gap: 0.417vw;
}

.filter-types-area button {
  padding: 0.313vw 0.417vw;
  color: #fff;
  font-weight: 500;
  font-size: 0.625vw;
  line-height: 0.938vw;
  border-radius: 0.208vw;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  height: max-content;
}

.emoji-area {
  position: absolute;
  flex: 1;
  right: 0;
  width: 15vw;
  height: 9.688vw;
  padding: 0.417vw;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 0.208vw;
  display: flex;
  gap: 0.417vw;
  flex-wrap: wrap;
  overflow: auto;
  transition: all 0.2s ease-in-out;
}

.emoji-item {
  cursor: pointer;
  opacity: 0.5;
  transition: 0.2s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.emoji-item:hover {
  opacity: 1;
  transform: scale(1.05);
}

.commandPrompt {
  align-items: center;
  display: flex;
  flex: 1;
  border-radius: 0.208vw;
}

.commandText {
  font-size: 0.625vw;
  color: #fff;
  font-weight: 600;
  padding: 0.208vw;
  border-radius: 0.208vw;
  background-color: rgba(255, 255, 255, 0.08);
  margin-left: 0.573vw;
}

.chatTypeBackground {
  text-transform: uppercase;
  background: rgba(255, 255, 255, 0.3);
  opacity: 0.4;
  letter-spacing: 0.031vw;
}

.selected {
  background: #c38a10;
  opacity: 1;
}

.chamado {
  background: #3a85fa;
}

.Admin {
  background: rgba(255, 53, 41, 0.75);
}

.ajuda {
  background: rgba(246, 157, 39, 0.75);
}

.megaphone {
  background: rgba(127, 0, 247, 0.952);
}

.advertencia {
  background: rgba(255, 53, 41, 0.8);
}
</style>
